/**
 * Returns a two-dimensional array according to `grid`
 * size.
 *
 * ```js
 * const a = Grids.toArray({ rows: 3, cols: 2 });
 * Yields:
 * [ [_,_] ]
 * [ [_,_] ]
 * [ [_,_] ]
 * ```
 *
 * `initialValue` can be provided to set the value
 * for all cells.
 * @param grid Grid
 * @param initialValue Initial value
 * @returns
 */
export const toArray2d = (grid, initialValue) => {
    const returnValue = [];
    for (let row = 0; row < grid.rows; row++) {
        returnValue[row] = Array.from({ length: grid.cols });
        if (initialValue) {
            for (let col = 0; col < grid.cols; col++) {
                returnValue[row][col] = initialValue;
            }
        }
    }
    return returnValue;
};
