import { findMinimum } from "./find-minimum.js";
/**
 * Returns the left-most of the provided points.
 *
 * Same as:
 * ```js
 * findMinimum((a, b) => {
 *  if (a.x <= b.x) return a;
 *  return b;
 *}, ...points)
 * ```
 *
 * @param points
 * @returns
 */
export const leftmost = (...points) => findMinimum((a, b) => (a.x <= b.x ? a : b), ...points);
/**
 * Returns the right-most of the provided points.
 *
 * Same as:
 * ```js
 * findMinimum((a, b) => {
 *  if (a.x >= b.x) return a;
 *  return b;
 *}, ...points)
 * ```
 *
 * @param points
 * @returns
 */
export const rightmost = (...points) => findMinimum((a, b) => (a.x >= b.x ? a : b), ...points);
