import { area } from "./area.js";
import { centroid } from "./centroid.js";
import { perimeter } from "./perimeter.js";
/**
 * Returns the largest circle enclosed by triangle `t`.
 * @param t
 */
export const innerCircle = (t) => {
    const c = centroid(t);
    const p = perimeter(t) / 2;
    const a = area(t);
    const radius = a / p;
    return { radius, ...c };
};
