import { Empty } from "./empty.js";
import { getPointParameter } from "./get-point-parameter.js";
import { isPoint } from "./guard.js";
const length = (ptOrX, y) => {
    if (isPoint(ptOrX)) {
        y = ptOrX.y;
        ptOrX = ptOrX.x;
    }
    if (y === undefined)
        throw new Error(`Expected y`);
    return Math.hypot(ptOrX, y);
};
/**
 * Normalise point as a unit vector.
 *
 * ```js
 * normalise({x:10, y:20});
 * normalise(10, 20);
 * ```
 * @param ptOrX Point, or x value
 * @param y y value if first param is x
 * @returns
 */
export const normalise = (ptOrX, y) => {
    const pt = getPointParameter(ptOrX, y);
    const l = length(pt);
    if (l === 0)
        return Empty;
    return Object.freeze({
        ...pt,
        x: pt.x / l,
        y: pt.y / l,
    });
};
