import { isPoint } from "../point/guard.js";
/**
 * Returns true if `p` is a valid line, containing `a` and `b` Points.
 * ```js
 * Lines.isLine(l);
 * ```
 * @param p Value to check
 * @returns True if a valid line.
 */
export const isLine = (p) => {
    if (p === undefined)
        return false;
    if (p.a === undefined)
        return false;
    if (p.b === undefined)
        return false;
    if (!isPoint(p.a))
        return false;
    if (!isPoint(p.b))
        return false;
    return true;
};
/**
 * Returns true if `p` is a {@link PolyLine}, ie. an array of {@link Line}s.
 * Validates all items in array.
 * @param p
 * @returns
 */
export const isPolyLine = (p) => {
    if (!Array.isArray(p))
        return false;
    const valid = !p.some(v => !isLine(v));
    return valid;
};
/**
 * Throws an exception if:
 * * line is undefined
 * * a or b parameters are missing
 *
 * Does not validate points
 * @param line
 * @param name
 */
export const guard = (line, name = `line`) => {
    if (line === undefined)
        throw new Error(`${name} undefined`);
    if (line.a === undefined)
        throw new Error(`${name}.a undefined. Expected {a:Point, b:Point}. Got: ${JSON.stringify(line)}`);
    if (line.b === undefined)
        throw new Error(`${name}.b undefined. Expected {a:Point, b:Point} Got: ${JSON.stringify(line)}`);
};
