import { guard } from "./guard.js";
import { numberTest, resultThrow } from "@ixfx/guards";
export const normalise = (c) => {
    //guard(v, `v`);
    if (c.distance === 0)
        throw new Error(`Cannot normalise vector of length 0`);
    return Object.freeze({
        ...c,
        distance: 1,
    });
};
/**
 * Clamps the magnitude of a vector
 * @param v
 * @param max
 * @param min
 * @returns
 */
export const clampMagnitude = (v, max = 1, min = 0) => {
    let mag = v.distance;
    if (mag > max)
        mag = max;
    if (mag < min)
        mag = min;
    return Object.freeze({
        ...v,
        distance: mag,
    });
};
/**
 * Calculate dot product of two PolarCoords.
 *
 * Eg, power is the dot product of force and velocity
 *
 * Dot products are also useful for comparing similarity of
 *  angle between two unit PolarCoords.
 * @param a
 * @param b
 * @returns
 */
export const dotProduct = (a, b) => {
    guard(a, `a`);
    guard(b, `b`);
    return a.distance * b.distance * Math.cos(b.angleRadian - a.angleRadian);
};
/**
 * Multiplies the magnitude of a coord by `amt`.
 * Direction is unchanged.
 * @param v
 * @param amt
 * @returns
 */
export const multiply = (v, amt) => {
    guard(v);
    resultThrow(numberTest(amt, ``, `amt`));
    return Object.freeze({
        ...v,
        distance: v.distance * amt,
    });
};
/**
 * Divides the magnitude of a coord by `amt`.
 * Direction is unchanged.
 * @param v
 * @param amt
 * @returns
 */
export const divide = (v, amt) => {
    guard(v);
    resultThrow(numberTest(amt, ``, `amt`));
    return Object.freeze({
        ...v,
        distance: v.distance / amt,
    });
};
