;
/**
 * Multiplies all components by `v`.
 * Existing properties of `pt` are maintained.
 *
 * ```js
 * multiplyScalar({ x:2, y:4 }, 2);
 * // Yields: { x:4, y:8 }
 * ```
 * @param pt Point
 * @param v Value to multiply by
 * @returns
 */
export const multiplyScalar = (pt, v) => {
    return isPoint3d(pt) ? Object.freeze({
        ...pt,
        x: pt.x * v,
        y: pt.y * v,
        z: pt.z * v,
    }) : Object.freeze({
        ...pt,
        x: pt.x * v,
        y: pt.y * v,
    });
};
